:root {
  font-size: 16px;
  --far-background-color: #cccccc;
  --close-background-color: #eeeeee;
  --text-on-primary-color: #eeeeee;
  --primary-color: #020100;
  --secondary-color: #801a86;
  --accent-color: #e768f3;
}
body {
  background-color: var(--far-background-color);
  font-family: 'Space Grotesk', sans-serif;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-attachment: fixed;
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
}
header {
  background-color: var(--primary-color);
  padding: 0 2rem 1rem 2rem;
}
nav {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-areas: "home menu logo logo cheese contact";
  gap: 2rem;
}
nav .header-link {
  color: var(--text-on-primary-color);
  font-size: 2rem;
  text-decoration: none;
  align-self: end;
  cursor: pointer;
  -webkit-user-select: none;
}
nav .header-link:nth-of-type(1),
nav .header-link:nth-of-type(2),
nav .header-link:nth-of-type(3){
  justify-self: start;
}
nav .header-link:nth-of-type(4),
nav .header-link:nth-of-type(5),
nav .header-link:nth-of-type(6){
  justify-self: end;
}
nav .header-link:nth-of-type(1) {
  grid-area: home;
  }
nav .header-link:nth-of-type(2) {
  grid-area: menu;
  }

